import uniIcon from '../uni-icon/uni-icon.vue'
export default {
	name: "uni-notice-bar",
	components: {
		uniIcon
	},
	props: {
		text: String,
		moreText: String,
		backgroundColor: {
			type: String,
			default: '#fffbe8'
		},
		speed: { //默认1s滚动100px
			type: [String, Number],
			default: 100
		},
		color: {
			type: String,
			default: '#de8c17'
		},
		single: { //是否单行
			type: [String, Boolean],
			default: false
		},
		scrollable: { //是否滚动，添加后控制单行效果取消
			type: [String, Boolean],
			default: true
		},
		showIcon: { //是否显示左侧icon
			type: [String, Boolean],
			default: true
		},
		showGetMore: { //是否显示右侧查看更多
			type: [String, Boolean],
			default: false
		},
		showClose: { //是否显示左侧关闭按钮
			type: [String, Boolean],
			default: false
		}
	},
	data() {
		const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			elId: elId,
			show: true,
			animation: ''
		}
	},
	watch: {
		text(newValue, oldValue) {
			this.$nextTick(() => {
				setTimeout(this.setAnimation, 200)
			})
		}
	},
	computed: {
		setTextClass() {
			let classList = []
			if (this.scrollable === true || this.scrollable === 'true') {
				classList.push('uni-noticebar--scrollable')
			} else {
				if (this.single === 'true' || this.single === true || this.moreText) {
					classList.push('uni-noticebar--single')
				}
			}
			return classList
		},
		setContenClass() {
			let classList = []
			if (this.scrollable === true || this.scrollable === 'true' || this.single === 'true' || this.single === true ||
				this.moreText) {
				classList.push('uni-noticebar--flex')
			}
			return classList
		}
	},






	onReady() {
		this.setAnimation()
	},

	methods: {
		clickMore() {
			this.$emit('getmore')
		},
		onClick(e) {
			let clientX = e.touches ? (e.touches[0] ? e.touches[0].clientX : e.changedTouches[0].clientX) : e.detail.clientX;
			if (uni.upx2px(48) + 12 > clientX && (String(this.showClose) === 'true')) {
				this.show = false
				this.$emit('close')
			}
			this.$emit('click')
		},
		setAnimation() {
			if (this.scrollable === false || this.scrollable === 'false') {
				return;
			}















			uni.createSelectorQuery().in(this).select(`#${this.elId}`).boundingClientRect().exec((ret) => {
				this.animation = `notice ${ret[0].width / this.speed}s linear infinite both`;
			});

		}
	}
}
